const OPEN_BASE_MODAL = 'OPEN_BASE_MODAL';
const CLOSE_BASE_MODAL = 'CLOSE_BASE_MODAL';
const SET_MODAL_ENTITY = 'SET_MODAL_ENTITY';
const CLEAR_MODAL_ENTITY = 'CLEAR_MODAL_ENTITY';
const OPEN_VIDEO_MODAL = 'OPEN_VIDEO_MODAL';
const CLOSE_VIDEO_MODAL = 'CLOSE_VIDEO_MODAL';
const state = {
    isShowBaseModal: false,
    baseModalEntity: null,
    isShowVideoModal: false
};

const mutations = {
    SET_MODAL_ENTITY(state, payload) {
        state.baseModalEntity = payload;
    },
    OPEN_BASE_MODAL(state) {
        state.isShowBaseModal = true;
    },
    CLOSE_BASE_MODAL(state) {
        state.isShowBaseModal = false;
    },
    OPEN_VIDEO_MODAL(state) {
        state.isShowVideoModal = true;
    },
    CLOSE_VIDEO_MODAL(state) {
        state.isShowVideoModal = false;
    }
};

const actions = {
    openBaseModal({commit}, payload) {
        if (payload) {
            commit(SET_MODAL_ENTITY, payload)
        }
        commit(OPEN_BASE_MODAL);
    },
    closeBaseModal({commit}) {
        commit(CLEAR_MODAL_ENTITY);
        commit(CLOSE_BASE_MODAL);
    },
    openVideoModal({commit}) {
        commit(OPEN_VIDEO_MODAL);
    },
    closeVideoModal({commit}) {
        commit(CLOSE_VIDEO_MODAL);
    }
};

export default {
    namespaced: true,
    state,
    mutations,
    actions
}
