import axios from 'axios';

export const api = axios.create({
    baseURL: process.env.VUE_APP_API_URL
});

async function getGroup(params = {}) {
    const response = await api.get('group/search', {params})
        .catch(e => {throw new Error(e); });
    if (response) {
        return response.data.data;
    }
}

async function getWorkshops(params = {}) {
    const response = await api.get('workshops/search', {params})
        .catch(e => {throw new Error(e); });
    if (response) {
        return response.data.data;
    }
}

async function submitLead(payload = {}) {
    const response = await api.post('lead/submit', payload)
        .catch(e => {throw new Error(e); });
    if (response) {
        return response;
    }
}

export {
    getGroup,
    getWorkshops,
    submitLead
};
