import Vue from 'vue';
import VueRouter from 'vue-router';

const HomePage = () => import('./pages/Home.vue');
const LifestylePage = () => import('./pages/Lifestyle.vue');
const MainScreen = () => import('./pages/MainScreen');
const UnderwaterPage = () => import('./pages/UnderwaterPage.vue');
const WorkshopPage = () => import('./pages/WorkshopPage.vue');
const TeacherTrainingCentrePage = () => import('./pages/TeacherTrainingCentrePage.vue');
const TeensAndKidsPage = () => import('./pages/TeensAndKidsPage.vue');


Vue.use(VueRouter);

const routes = [
    {
        path: '/',
        name: 'home',
        component: HomePage
    },
    {
        path: '/lifestyle',
        name: 'lifestyle',
        component: LifestylePage
    },
    {
        path: '/main-screen',
        name: 'main_screen',
        component: MainScreen
    },
    {
        path: '/underwater/group/:id',
        name: 'underwater',
        component: UnderwaterPage
    },
    {
        path: '/workshop',
        name: 'workshop',
        component: WorkshopPage
    },
    {
        path: '/teacher-training',
        name: 'teacher_training',
        component: TeacherTrainingCentrePage
    },
    {
        path: '/teens-kids',
        name: 'teens_and_kids',
        component: TeensAndKidsPage
    },
];

const router = new VueRouter({
    routes,
    mode: 'history'
});

export default router;
