<script>
    import {
        getGroup
    } from "../../api";
    import { mapActions } from 'vuex';
    export default {
        components: {},
        props: ['propFormData'],
        data() {
            return {
                formData: {
                    title: 'Обераємо программу саме для <br>вирішення вашої проблеми',
                    chb_1: 'Щоб підтримати свій рівень',
                    chb_2: 'Щоб прокачати свій рівень'
                },
                data: {
                    "selected": '',
                    "group" : null
                },
                groups: null
            };
        },
        methods: {
            ...mapActions('modals', ['openBaseModal']),
            changeCheckbox(index) {
                this.data.selected = index;

                let result = this.groups.filter(obj => {
                    return obj['course'] === index && ((new Date(obj['start_date']).getTime()) > new Date().getTime())
                })[0];

                this.data.start_date = result.start_date;
                this.data.price = result.price;
                this.data.discount_price = result.discount_price;
                this.data.id = result.id;

                this.data.data = result;

                console.log(this.data.data);
            },
            openModal() {
                this.openBaseModal(this.data);
            },
            getStartDate(date) {
                return new Date(date).toLocaleDateString();
            }
        },
        created() {
            if(this.propFormData) {
                this.formData = (this.propFormData)
            }
            getGroup().then(
              result => {
                  this.groups = result;
                  this.changeCheckbox('lifestyle');
              },
              error => {
                  console.warn(error);
              }
            );
        },
        mounted() {
        }
    }
</script>
