const Cookies = {
    set: function (name, value, ttl, domain) {
        var expires = "";
        var cookieDomain = "";
        if (ttl) {
            var date = new Date();
            date.setTime(date.getTime() + (ttl * 60 * 1000));
            expires = "; expires=" + date.toGMTString();
        }
        if (domain) {
            cookieDomain = "; domain=" + domain;
        }
        document.cookie = name + "=" + escape(value) + expires + cookieDomain + "; path=/";
    },
    get: function (name) {
        var i, c;
        var nameEQ = name + "=";
        var ca = document.cookie.split(';');
        for (i = 0; i < ca.length; i++) {
            c = ca[i];
            while (c.charAt(0) === ' ') {
                c = c.substring(1, c.length);
            }
            if (c.indexOf(nameEQ) === 0) {
                return unescape(c.substring(nameEQ.length, c.length));
            }
        }
        return null;
    }
};

const Tracker = {
    visitTtl: 4 * 60,
    trackCookieName: 'ww_track',
    trackData: {},
    track: function () {
        if (this.getCookie(this.trackCookieName)) {
            console.log('[wwt] skipping...')
        } else {
            this.trackData = {
                time: new Date().getTime() / 1000.0,
                landing_page: window.location.href,
                referrer: (document.referrer.length > 0) ? document.referrer : null,
            };
            this.setCookie(this.trackCookieName, JSON.stringify(this.trackData));
        }
    },
    getTrackData: function () {
        if (this.getCookie(this.trackCookieName)) {
            var track = JSON.parse(this.getCookie(this.trackCookieName));
            track['source_page'] = window.location.href;
            return track;
        }
        return {};
    },
    setCookie: function (name, value) {
        Cookies.set(name, value, this.visitTtl);
    },
    getCookie: function (name) {
        return Cookies.get(name);
    },
    destroyCookie: function (name) {
        Cookies.set(name, "", -1);
    }
};

export default Tracker;